@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

*{
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#home{height:100vh;max-width:90vw;margin:10vh auto auto auto;display:flex;justify-content:space-around}#home #homeDescription{margin-left:10vw;max-width:50vw;text-align:center;margin:auto;padding-bottom:10vh}#home #homeDescription h1{line-height:5rem;text-align:right}#home #homeDescription h3{margin-top:10vh;overflow-wrap:break-word;vertical-align:middle;line-height:3rem}#home #homeDescription #checkOut{text-decoration:none}#home #homeLogo{max-width:40vw}

header{margin-top:1.25rem;display:flex;justify-content:space-between}header a{font-size:1.125rem}header #logo{font-size:1.75rem;font-weight:bold;float:left;margin:0 5vw 0 5vw;text-decoration:none;color:#003CA8}header #logo :hover{color:inherit}

#products{margin-top:6.25rem;display:flex}.ant-carousel{width:80vw;margin:auto}#products .leftArrow,#products .rightArrow{width:15vw;font-size:2.5rem}.displayItem{display:flex;justify-content:space-around}.displayImg{display:flex;justify-content:center;align-items:center;margin-left:1.25rem}.displayDesc{width:40%}.leftArrow,.rightArrow{margin:auto;font-size:1.25rem}

#contact{margin-top:6.25rem;display:flex;margin-bottom:12.5rem}#contact-leftPane{width:40%;margin:auto}#contact-rightPane{width:40%;margin:auto;margin-top:1.875rem}#contact-img{width:20%;max-width:12.5rem;margin-right:6.25rem}

